body {
  margin: 0;
  background: #faf8f0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.board-table {
  border-spacing: 8px;
  border-collapse: separate;
  background: #bbada0;
  border-radius: 4px;
}

.board-table tr td {
  width: 60px;
  height: 60px;
}

.app-btn {
  background: brown;
  color: #fff;
  border: 0;
  border-radius: 4px;
  padding: 8px 12px;
  font-weight: bold;
}

.app-text-primary {
  color: #776e65;
}
